.createTodo {
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
  margin: 40px 40px 0px;
  padding: 40px 40px 0px;
  display: flex;
  flex-direction: column;
  width: 400px;
}
.createTodo h3 {
  font-size: 1.5rem;
  margin-bottom: 50px;
  margin-top: 50px;
}
.createTodo label {
  margin-bottom: 20px;
  font-size: 0.8rem;
}
.createTodo input {
  border-radius: 15px;
  border: 0;
  padding-left: 20px;
  background-color: rgb(240, 240, 240);
  height: 40px;
  margin-bottom: 20px;
}
.createTodo button {
  width: 35%;
  border-radius: 10px;
  background-color: rgb(97, 39, 89);
  color: white;
  border: 0;
  height: 35px;
  font-size: 0.75rem;
  cursor: pointer;
  z-index: 1;
}
.createTodo img {
  margin-top: 80px;
}


