* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  height: 100vh;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(34,193,195);
  background: linear-gradient(-45deg, rgb(226, 139, 177) 0%, rgb(235, 211, 222) 40%, rgb(206, 219, 235) 80%,  rgb(162, 190, 223)100%);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-family: 'Unbounded', cursive;
}

#root {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.divList {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: 20px;
  width: 400px;
}




